import React, { Component } from "react";
import PropTypes from "prop-types";
import SchoolForm from "./presenter";

class Container extends Component {
  state = {
    id:null,
    name:"",
    location:"",
    image:null,
    file:null,
    loading:true,
    action:"new"
  };
  static propTypes = {
    goToSchool: PropTypes.func.isRequired,
    goToHome: PropTypes.func.isRequired,
  };
  componentDidMount() {
      if(this.props.location.pathname==="/school/new"){
        //  
        this.setState({
          action: "new"
        });
      }else if (this.props.location.pathname.match(/^\/school\/(\d+)\/edit/)){
        //  
        fetch(`/api/schools/${this.props.match.params.schoolId}/`, {
          headers: {
            Authorization: `JWT ${this.props.token}`,
            "Content-Type": "application/json"
          }
        })
        .then(response => {
          return response.json();
        }).then(json =>{
          if (json.is_manager===false){
            return this.props.goToHome()
          }
          this.setState({
            id:json.id,
            name:json.name,
            image:json.image,
            location:json.location,
            action:"edit"
          });
        })
      }else{
        //  
        return this.props.goToHome()
        
      }
  }
    _handleInputChange = event => {
      const { target: { value, name } } = event;
      this.setState(
      {
          [name]: value
      });
    };
    _handleSubmit = event => {
      const { name, location, file, action } = this.state;
      const {match: { params }, token }= this.props
      event.preventDefault();
      if(!name){
        this.setState({
          errorMessage: "   ."
        });
        return
      }
      if(!location){
        return this.setState({
          errorMessage: " ."
        });
      }
      const formData = new FormData()
      formData.append('name',name)
      formData.append('location',location)
      if(file){
        const files = Array.from(file)
        files.forEach((file, i) => {
          formData.append('image', file)
        })
      }
      let url
      let method
      let message
      if(action==="new"){
        url = `/api/schools/`
        method = "POST"
        message = ""
      }else if(action==="edit"){
        url = `/api/schools/${params.schoolId}/`
        method = "PUT"
        message = ""
      }
      fetch(url, {
        method: method,
        body: formData,
          headers: {
          Authorization: `JWT ${token}`
        }
      })
      .then(response => {
        return response.json();
      }).then(json =>{
        alert(`${message}!`)
        this.props.goToSchool(json.id)
      })
    };
  _onChange = (e) =>{
      this.setState({
        image: URL.createObjectURL(e.target.files[0]),
        file: e.target.files
      })
  }


  render() {
    return (
      <SchoolForm 
          {...this.state} 
          onChange = {this._onChange} 
          handleInputChange = {this._handleInputChange} 
          handleSubmit = {this._handleSubmit} 
      />
    );
  }
}

export default Container;